/******************************************************************************
* Style sheet for the home page.
******************************************************************************/

.Home{
    display: flex;
    padding: 50px 50px 50px 50px;
}

.home_container{
    display: flex;
    flex-direction: column; 
    justify-content: space-evenly; 
    flex-flow: row wrap;
}

.resume_link a{
    display: flex;
    font-size: 16px;
    margin: 20px;
    padding: 20px 40px;
    border: none;
    max-width: 100px;
    border: 2px solid black;
    color: black;
    text-align: center;
    text-decoration: none;
}

.resume_link {
    margin-top: 60px;
}

.resume_link:hover a{
    cursor: pointer;
    background-color: cornflowerblue; 
    color: white;
}

.column{
    flex: 50%;
}

#profile_pic {
    margin-top: 40px;
    max-height: auto;
    max-width: 100%;
    object-fit: cover;
    vertical-align: bottom;
}

.container_info {
    color: black;
    margin-left: 0px;
    margin-right: 10px;
    text-align: middle;
}

.container_info h3 {
    padding: 10px 10px 10px 10px;
}

.container_info h4 {
   padding: 10px 10px 10px 10px;
}

/*After publishing, profile pic half loads on iphone.
    --going to try to add a margin to it*/
@media screen and (max-width: 870px){
    .Home{
        margin-bottom: 50px;
    }

}

/*Image size still too large for Large screens, loading slow*/
@media screen and (min-width: 1800px){
    #profile_pic {
        height: auto;
        max-width: 75%;
    }
}